@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400&display=swap");
* {
  box-sizing: border-box;
}

.App {
  width: 100vh;
  min-height: 100vh;
  margin: 0 auto;
  font-family: "Montserrat";
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.wrap {
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heading {
  text-transform: uppercase;
  font-size: 40px;
  margin: 52px 0 32px;
  z-index: 1;
  text-align: center;
}

/*form*/

form {
  margin-bottom: 52px;
  border: 2px solid #000;
  overflow: hidden;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  max-width: 520px;
}

form input {
  width: 50%;
  padding: 10px;
  line-height: 1;
  font-size: 16px;
  border: 0;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
  border-radius: 0;
  background: #fff;
}

form input:focus {
  outline: 0;
  background: #fef2de;
}

form input:last-of-type {
  width: 100%;
}

button {
  width: 100%;
  border: 0;
  font-size: 16px;
  cursor: pointer;
}

button,
input[type="submit"] {
  text-transform: uppercase;
  padding: 8px 15px;
  border: none;
  background: #f00;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.pizza {
  background: white;
  border: 1px solid #ededed;
  box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.09);
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 12px;
  margin-bottom: 22px;
}

.pizza img {
  width: 252px;
  height: 200px;
  object-fit: contain;
}

.pizza span {
  background: #ff0000;
  color: white;
  font-weight: 600;
  padding: 5px;
  line-height: 1;
  font-size: 27px;
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
}

.pizza .pizza-controls {
  position: absolute;
  top: 5px;
  left: 5px;
}
.pizza svg {
  width: 22px;
  height: 22px;
  cursor: pointer;
  margin-right: 10px;
  color: #392e4a;
}

.pizza a {
  color: #fff;
  text-decoration: none;
}

.pizza h2 {
  margin: 0 40px;
  text-align: center;
  margin-top: -1rem;
  background: red;
  display: inline;
  line-height: 1.3;
  font-size: 27px;
  color: white;
  padding: 0 7px;
}

/* Пагинация */
.pagination {
  text-align: center;
  display: inline-grid;
  grid-template-columns: repeat(4, auto);
  align-items: stretch;
  justify-content: center;
  align-content: center;
  margin: 52px 0 20px;
  border: 1px solid #e1e1e1;
  border-radius: 10px;
}

.pagination > * {
  margin: 0;
  padding: 15px 20px;
  border-right: 1px solid #e1e1e1;
}
.pagination :first-child {
  cursor: pointer;
}
.pagination :last-child {
  cursor: pointer;
  border-right: 0;
}

.pagination .disabled {
  pointer-events: none;
  color: #f2f2f2;
}

/* Edit Form */

form .edit-form {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.edit-form input {
  width: 100%;
}

/* Страница каждой пиццы */
.pizza-page img {
  width: 420px;
  height: 100%;
  object-fit: contain;
}
.pizza-page h2 {
  font-size: 52px;
  margin-top: -3rem;
}
.pizza-page p {
  font-size: 22px;
  line-height: 2;
  text-align: center;
}
.pizza-page span {
  font-size: 42px;
  transform: rotate(4deg);
}
